.stars {
  display: flex;
}

.stars__star {
  width: 16px;
  height: 16px;
  background-image: url(../images/star.svg) no-repeat center;

  &:not(:last-child) {
    margin-right: 4px;
  }
}

.stars--1 :first-child,
.stars--2 :nth-child(-n + 2),
.stars--3 :nth-child(-n + 3),
.stars--4 :nth-child(-n + 4),
.stars--5 :nth-child(-n + 5) {
  background: url(../images/star-active.svg) no-repeat center;
}
